body {
    min-width: 300px;
}

.auth {
    max-width: 500px !important;
}

#form_div {
    padding: 10px;
}

.setborder {
    border-radius: 12px !important;
    border: 5px solid rgb(49, 53, 97) !important;

}

.form_input {
    padding: 10px;
    font-size: 14px;
    margin-top: 20px;
    border-top: none;
    border-left: none;
    border-radius: 0px;
    border-right: none;
}

.form_btn {
    color: #fff;
    padding: 1px 20px;
    border-radius: 5px;
    background-color: rgb(66, 31, 66);
}

#edit_todo_form {
    top: 20%;
    width: 80% !important;
    position: fixed !important;
    background: rgb(174, 175, 115) !important;
}

@media screen and (max-width :768px) {
    #edit_todo_form {
        margin-top: 10% !important;
        width: 90% !important;
    }

}